  .Myapplications{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 150px 0;
  }
  .Myapplication-1 li{
    display: flex;
    margin: 50px;
    gap: 0 20px;
    align-items: center;
  }
  .application_number{
    padding: 13px 15px;
    border: 1px solid #151a26;
    color: #151a26;
    position: relative;
    border-radius: 50px;

  }

  .application_number1{
    padding: 10px 15px;
    background-color: #DBA953;
    border-radius: 50px;
    border: 1px solid #DBA953 !important;
    color: #fff;
  }
  .application_number1::before{
    content: "";
    width: 1px;
    height: 60%;
    position: absolute;
    display: block;
    background-color: #151a26;
    z-index: 1;
    bottom: -70%;
    left: 50%;
  }
  .application_number2::before{
    content: "";
    width: 1px;
    height: 60%;
    position: absolute;
    display: block;
    background-color: #151a26;
    z-index: 1;
    bottom: -70%;
    left: 50%;
  }

  .Myapplication-1{
    width: 35%;
    display: inline-block;
    background-color: #fff;
    margin-left: 20px;
  }

  .form-wrap {
    background-color: white;
    /* padding: 45px 0px; */
    width: 60%;
    display: inline-block;
    min-height: 430px;
    margin: 0% auto;
  }
 
  .form-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 20px;
    margin-left: 8%;
    /* margin-right: auto; */
    padding: 30px;
    margin: auto;
    width: 80%;
    grid-column-gap: 170px;
  }
 
  .input_wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
 
  .btns_wrap {
    width: 100%;
    padding: 0 100px;
    height: 92px;
    /* margin: 0% 10%; */
    background: #FBFBFB;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .btns_wrap Button{
    border: 1px solid #000;
    border-radius: 20px;
    padding:5px 15px;
    color: #000;
  }
  .btns_wrap Button:nth-child(1) a{
    color: #000 !important;
  }
  .btns_wrap Button:nth-child(2){
    background-color: #DBA953 !important;
    border: 1px solid #DBA953 !important;
    color: #fff !important;
  }
  .css-13xfq8m-MuiTabPanel-root{
    padding: 0 !important;
  }
  .eligibility_login a{
    color: #fff;
  }
  .Eligibility_page a{
    color: #fff;
  }
