}
.eligibility_tab{
  cursor: pointer;
}
  @media only screen and (max-width:1024px){
    .eligibility_calculator_cont, .eligibility_mortgage_cont{
      width: 100%;
    }
    .overview_header > div >div{
      width: 100%;
      padding: 0 20px;
    }
    .overview_header .header_logo{
      width: 120px;
      padding: 20px 0 0 0 ;
    }
    .form-content{
      grid-column-gap: 50px;
    }
    .Myapplication-1 h2{
      font-size: 22px;
    }
  }
  @media only screen and (max-width:768px){
    .eligibility_cal_left{
      width: 100%;
    }
    .eligibility_cal_right{
      width: 100%;
    }
    .eligibility_mortgage_cont > div{
      width: 100%;
    }
    .eligibility_title_img{
      width: 20%;
    }
    .eligibility_title h1{
      width: 80%;
    }
    .eligibility_body_left,.eligibility_body_right{
      width: 100%;
    }
    .Myapplication-1{
      width: 60%;
      margin-left: 0;
    }
    .Myapplications .form-wrap{
      width: 80%;
    }
    .overview_header{
      display: none;
    }
  }
  @media only screen and (max-width:480px){
    .eligibility_downpayment div:nth-child(1){
      width: 65%;
    }
    .eligibility_downpayment div:nth-child(2){
      width: 35%;
    }
  }

